#movingPlanet {
  height: 400px;
  width: 400px;
  position: relative;

  .orbit {
    border: solid;
    border-color: var(--theme-inverse-color) transparent transparent transparent;
    border-radius: 50%;
    border-width: 1px 1px 0 0;
    box-sizing: border-box;
    position: absolute;
    transform: rotate(0deg);
    transform-origin: center;

    &#earth-orbit {
      -webkit-animation: orbit 36.5s linear infinite;
      animation: orbit 36.5s linear infinite;
      height: 300px;
      left: 50px;
      top: 50px;
      width: 300px;
    }

    &#moon-orbit {
      -webkit-animation: orbit 2.7s linear infinite;
      animation: orbit 2.7s linear infinite;
      height: 80px;
      left: -25px;
      top: -25px;
      width: 80px;
    }

    .globe {
      border-radius: 50%;
      position: absolute;

      &#earth {
        background: aqua;
        box-shadow: 0 0 5px var(--theme-inverse-color);
        height: 30px;
        right: 28px;
        top: 28px;
        width: 30px;
      }

      &#moon {
        background: var(--theme-inverse-color);
        box-shadow: 0 0 2px var(--theme-inverse-color);
        height: 12px;
        right: 2px;
        top: 8px;
        width: 12px;
      }
    }


  }
}

#movingPlanet__sun_background {
  background-color: hsl(41, 99%, 75%);
  border-radius: 50%;
  box-shadow: 0 0 20px rgba(hsl(41, 99%, 75%),0.9);
  height: 100px;
  left: 150px;
  position: absolute;
  top: 150px;
  width: 100px;
}

#movingPlanet__sun {
  background-color: hsl(41, 99%, 75%);
  border-radius: 50%;
  box-shadow: 0 0 30px rgba(hsl(41, 99%, 75%),0.7);
  height: 100px;
  left: 150px;
  position: absolute;
  top: 150px;
  width: 100px;

  &::after {
    content: "";
    width: 25px;
    height: 25px;
    position: absolute;
    background-color: hsl(33, 95%, 73%);
    z-index: 99;
    border-radius: 50%;
    top : 20px;
    left: 20px;
  }

  &::before {
    content: "";
    width: 15px;
    height: 15px;
    position: absolute;
    background-color: hsl(33, 95%, 73%);
    z-index: 99;
    border-radius: 50%;
    top : 60px;
    left: 60px;
  }
}


@-webkit-keyframes orbit {
  to {
    transform: rotate(360deg);
  }
}

@keyframes orbit {
  to {
    transform: rotate(360deg);
  }
}
