.hover-button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  background: transparent;
  font-size: inherit;
  padding: 0;
  font-family: var(--font-family);
  width: 12rem;
  height: auto; }
  .hover-button .hover-button-text {
    transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 0.75rem 0;
    margin: 0 0 0 .5rem;
    font-weight: 700;
    line-height: 1.6;
    text-align: center;
    color: var(--theme-inverse-color); }
  .hover-button:hover .hover-button-circle {
    width: 100%; }
  .hover-button:hover .hover-button-text {
    color: var(--theme-background-color); }

.hover-button-circle {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  position: relative;
  display: block;
  margin: 0;
  width: 3rem;
  height: 3rem;
  background: var(--theme-inverse-color);
  border-radius: 1.625rem; }
  .hover-button-circle .hover-button-icon {
    transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto; }
    .hover-button-circle .hover-button-icon.hover-button-arrow {
      transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
      left: 0.625rem;
      width: 1.125rem;
      height: 0.125rem;
      background: none; }
      .hover-button-circle .hover-button-icon.hover-button-arrow::before {
        position: absolute;
        content: '';
        top: -0.25rem;
        right: 0.0625rem;
        width: 0.625rem;
        height: 0.625rem;
        border-top: 0.125rem solid var(--theme-background-color);
        border-right: 0.125rem solid var(--theme-background-color);
        transform: rotate(45deg);
        z-index: 1; }

