nsform-origin:left}@keyframes excite2{to{transform:rotateZ(5deg)}}.whale-body{height:80px;width:58px;border-radius:25px;position:absolute;bottom:-18px;left:54px;background-color:var(--whale-shadow-color);transform:rotateZ(-10deg);border:2px solid #333}.whale-drop{height:13px;width:13px;border-radius:50%;background-color:var(--whale-color);position:absolute;bottom:-25px;right:180px;animation:spray 3s ease-out infinite}.whale-drop::before{content:"";height:10px;width:10px;border-radius:50%;background-color:var(--whale-color);position:absolute;bottom:0;right:0;animation:spray 3s 0.3s ease-out infinite}.whale-drop::after{content:"";height:12px;width:12px;border-radius:50%;background-color:var(--whale-color);position:absolute;bottom:0;right:0;animation:spray 3s 0.7s ease-out infinite}@keyframes spray{0%{transform:translateY(0)}30%{transform:translateY(-100px)}60%{transform:translateY(0)}}.whale-lower{width:100%;height:200px;position:absolute;bottom:0;left:0;border-top:5px solid #333;overflow:hidden}.whale{width:400px;height:400px;border-radius:300px;position:absolute;top:-250px;left:50px;background-color:var(--whale-color);box-shadow:inset 0 -10px var(--whale-shadow-color);border:5px solid #333;box-sizing:border-box;overflow:hidden}.whale-eye{width:20px;height:20px;border-radius:50%;position:absolute;bottom:110px;right:120px;background-color:#333;animation:blink 2s linear infinite}@keyframes blink{0%{transform:scaleY(0.1)}5%{transform:scaleY(0.1)}5.1%{transform:scaleY(1)}}.whale-detail1{width:100px;height:100px;border-radius:50%;position:absolute;bottom:10px;right:0px;background-color:lightsteelblue;overflow:hidden;border:2px solid #333;box-sizing:border-box}.whale-fin{width:50px;height:120px;border-radius:40px;position:absolute;bottom:0;right:180px;background-color:var(--whale-color);border:5px solid #333;border-top:5px solid transparent;transform:rotateZ(15deg);box-shadow:inset 0 -10px var(--whale-shadow-color);animation:swim 2s linear infinite alternate;transform-orig